<template>
    <div>
        <div class="box">
            <div class="search-box clearfix" ref="serarchBox">
                <div class="search fl">
                    <van-search placeholder="请输入搜索关键词"  />
                </div>
                <div class="relase fr">
                    <span><van-icon name="add-o" size=".8rem"/></span>
                    <span>发布</span>
                </div>
            </div>

            <div class="slide-box" ref="slideBox">
                <van-swipe style="height: 200px;" vertical :autoplay="4000">
                    <van-swipe-item><img class="auto-img" src="../../assets/image/timg.jpg" alt=""></van-swipe-item>
                    <van-swipe-item><img class="auto-img" src="../../assets/image/timg2.jpg" alt=""></van-swipe-item>
                    <van-swipe-item><img class="auto-img" src="../../assets/image/timg3.jpg" alt=""></van-swipe-item>
                    <van-swipe-item><img class="auto-img" src="../../assets/image/timg4.jpg" alt=""></van-swipe-item>
                </van-swipe>
            </div>

            <div class="list-box" ref="listBox" @scroll="load">

                <div class="content" ref="content" v-for="(item,index) in listData" :key="index" @click="newsDetails(item.item_id,item)">
                    <div class="header clearfix">
                        <span class="fl">来源:{{item.source}}</span>
                        <span class="fr">{{item.chinese_tag}}</span>
                    </div>
                    <div class="news-content">
                        <div class="img fl">
                            <!-- <img class="auto-img" :src="item.middle_image" alt=""> -->
                            <img class="auto-img" :src="item.media_info.avatar_url" alt="">
                            <!-- <img class="auto-img" :src="item.middle_image.url" alt=""> -->
                        </div>
                        <div class="text fr">
                            <div class="title">{{item.title}}</div>

                            <div class="abstract">{{item.abstract}}</div>
                        </div>
                    </div>
                </div>

            </div>
        </div>
        <div>
             <router-view />
        </div>
    </div>
</template>

<script>

    import $ from 'jquery'

    export default {
        name:'homePage',

        data(){
            return {

                // 列表的所有数据
                alllistData:[],

                // 截取的数据
                listData:[],

                // 保存在本地的浏览历史记录
                historyData:[],

                // 首页缓存在本地的数据
                homePageData:[],

                // 每次懒加载的数量
                count:3,

                isload:true

            }
        },

        created(){
            let _this = this
            
            // 从本地获取首页的数据
            let homePageData = localStorage.getItem('homePageData')
            // homePageData = homePageData ? JSON.parse(homePageData) : []


            // 如果本地没有缓存数据
            if(!homePageData){

                 //加载提示
                this.$toast.loading({
                    duration: 0 ,
                    message: "加载中..."
                });

                this.axios({
                //请求类型
                method:'GET',
                //请求地址
                url:'http://api01.6bqb.com/toutiao/search?apikey=9152B061C54746870D248AA78B33EA05&tag=emotion'
                
                }).then(result =>{
                    // console.log('result ==>',result)
                    _this.homePageData = result.data
                    // console.log('_this.homePageData==>',_this.homePageData)

                    _this.alllistData = _this.homePageData.data
                    _this.listData = _this.alllistData.slice(0,_this.count)

                    //取消加载提示
                    this.$toast.clear();

                    // 保存在本地
                    localStorage.setItem('homePageData',JSON.stringify(_this.homePageData))
            
                }).catch(err => {
                    //取消加载提示
                    this.$toast.clear();
                })

            } else{
                _this.homePageData = JSON.parse(homePageData)
                _this.alllistData = _this.homePageData.data
                _this.listData = _this.alllistData.slice(0,_this.count)
                _this.count +=3
                // console.log('_this.homePageData',_this.homePageData)
               
            }

        },

        methods:{

            //点击跳转到详情页以及传参
            newsDetails(id,item){
                // this.$router.push({name:'newsDetails'})
                this.$router.push({
                    name: 'newsDetails',
                    params: {
                        id: id,
                        item:item
                    }
                })
                //获取历史记录的数据
                let historyData = localStorage.getItem('historyData')
                //防止空用户
                historyData = historyData ? JSON.parse(historyData) : [];

                // 增加到数组中
                historyData.unshift(item)
                
                // 保存在本地
                localStorage.setItem('homePageData',JSON.stringify(this.homePageData))

            },


            // 懒加载
            load(){
                let _this = this

                //保存当前滚动的所有定时器序号
                var timers = [];

                var clearTime = setTimeout(function () {
                    for (var i = 1; i < timers.length; i++) {
                        clearTimeout(timers[i]);
                    }

                    let serarchBox = _this.$refs.serarchBox.offsetHeight
                    // console.log('serarchBox ==>',serarchBox)

                    let slideBox = _this.$refs.slideBox.offsetHeight
                    // console.log('slideBox ==>',slideBox)

                    // 获取最后一个节点
                    let last = _this.$refs.content[_this.$refs.content.length -1]
                    // console.log('last ==>',last)

                    // 最后一个节点可视窗口的距离
                    let lastHeight = last.getBoundingClientRect();
                    let top = parseFloat(lastHeight.top)
                    // console.log('top ==>',top)

                    // 每个内容的高度
                    let contentHeight = _this.$refs.content[0].clientHeight
                    // console.log('contentHeight ==>',contentHeight)

                    // 大盒子的高度
                    let listBox = _this.$refs.listBox.offsetHeight
                    // console.log('listBox ==>',listBox)

                    let isPass = listBox - contentHeight + serarchBox + slideBox + 24
                    // console.log('isPass ==>',isPass)

                    if(isPass >=top){

                        _this.$toast.loading({
                            duration:0,
                            message: "加载中..."
                        });
                        
                        _this.listData = _this.alllistData.slice(0,_this.count)
                        _this.count +=3

                        if(_this.listData.length < 3){
                            _this.isLoad = false
                        }
                        //取消加载提示
                        _this.$toast.clear();       
                    }
                    timers = [];      
                })
                timers.push(clearTime);
            },

        }
    }
</script>

<style lang="less" scoped>
    .box{
        background: #f5f5f5;
    }
    .search-box{
        width:calc(100% - .6rem);
        height: 1.5rem;
        background: #e498bc;
        margin-top: 24px;
        padding:0 .3rem;

        .search{
            width:calc(100% - 1.2rem);
            margin-top: .15rem;
        }

        .van-search {
            padding: 0;
            height: 1.2rem;
            border-radius: .2rem;          
        }
        .van-search .van-cell {
                padding: 0;
                background: #fff;
        }
        .van-icon{
            height: .3rem;
        }
        .relase{
            width: .8rem;
            height: 1.2rem;
            margin-top: .15rem;
            color: #fff;
            font-size: .3rem;
            text-align: center;      
        }
    }

    .slide-box{
        .van-swipe{
            border-bottom-left-radius: .4rem;
            border-bottom-right-radius: .4rem;
            // margin-top: .1rem;
        }
    }

    .list-box{
        overflow-y: auto;
        height: 9rem;
    }

    .content{
        margin-bottom: .3rem;
        margin-top: .3rem;

        .header{
            width:calc(100% - 1rem);
            height: .6rem;
            background: #fff;
            line-height: .6rem;
            padding: 0 .5rem;
            >span:nth-child(1){
                margin-left: .3rem;
            }
            >span:nth-child(2){
                margin-right: .3rem;
            }
        }
        .news-content{
            width:calc(100% - 1rem);
            height: 3rem;
            background: #fff;
            line-height: .8rem;
            padding: 0 .5rem;

            .img{
                width: 3rem;
                height: 2.1rem;
                background: #ddd;
                margin-top: .45rem;
            }
            .text{
                width:calc(100% - 3.3rem);
                height: 100%;
                // background: #e498bc;

                .title{
                    font-size: .4rem;
                    font-weight: bold;
                    margin-top: .1rem;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                    
                }
                .abstract{
                    height: 1.47rem;
                    color: #595959;
                    line-height: .5rem;
                    font-size: .35rem;
                    margin-top: .3rem;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 3;
                    overflow: hidden;
                }
            }
        }


    }





    // .list-box{
    //     width:calc(100% - .6rem);
    //     height: 8.5rem;
